
.card-catalog {
     background-color: $color-bg-header;
}
 
.gallery {
    width: 100%;
    display: flex;
    gap: 20px;
    flex-direction: row;
    flex-wrap: wrap;

    @include tablet {
        padding-top: 52px;
        padding-bottom: 44px;
    }

    @include desktop {
        padding-top: 80px;
        padding-bottom: 60px;
    }
}

.wraper__txt {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;

    @include desktop {
        margin-bottom: 28px;
    }
}

.weekly__title {
    @include font-title ($fs: 16, $lh: 1.18);
    text-transform: uppercase;
    color: $color-text;

    @include tablet {
        font-size: 24px;
        line-height: 1.16;
    }

    @include desktop {
        font-size: 28px;
        line-height: 1.17;
    }
}

.weekly__link {
    @include font-title ($fs: 16, $lh: 1.18);
    color: $color-text-focus;

    @include tablet {
        font-size: 24px;
        line-height: 1.16;
    }

    @include desktop {
        font-size: 28px;
        line-height: 1.17;
    }
}



.article__item {
    position: relative;
    // display: flex;
    // gap: 16px;
    width: calc((100% - 70px)/4);

    @include tablet {
        width: 224px;
    }

    @include desktop {
        width: 395px;
    }
}



.article__img {
    margin: 0;
    border-radius: 5px;
}

.details {
    content: '';
    position: absolute;
    width: 280px;
    background: linear-gradient(180deg,
            rgba(0, 0, 0, 0) 63.48%,
            rgba(0, 0, 0, 0.9) 92.16%);
    border-radius: 5px;
    bottom: 0;
    left: 0;
    padding-top: 100%;
    padding-right: 12px;
    padding-bottom: 12px;
    padding-left: 12px;

    @include tablet {
        width: 224px;
    }

    @include desktop {
        width: 395px;
        padding-top: 100%;
        padding-right: 20px;
        padding-bottom: 20px;
        padding-left: 20px;
    }
}

.title__txt {
    @include font-title ($fs: 12, $lh: 1.66);
    text-transform: uppercase;
    color: $color-text;

    @include tablet {
        line-height: 1.5;
    }

    @include desktop {
        font-size: 20px;
        line-height: 1.15;
        margin-bottom: 8px;
    }
}

.movieGenres {
    @include font-title ($fs: 12, $lh: 1.66);
    color: $color-text-secondary;

    @include tablet {
        line-height: 1.5;
    }

    @include desktop {
        font-size: 20px;
        line-height: 1.15px;
    }
}

.rating__value {
    display: flex;
    width: 1px;
    margin-left: auto;
    margin-right: auto;

    font-size: 1px;
    color: transparent;
    text-align: center;
}

.wraper__details {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

// ------------- RATING------------
.rating {
    display: flex;
    align-items: flex-end;
    font-size: 14px;
    line-height: 0.75;

    @include tablet {
        font-size: 10px;
    }

    @include desktop {
        font-size: 18px;
    }
}

.rating__body {
    position: relative;
}

.rating__body::before {
    content: '✰✰✰✰✰';
    display: block;
    color: $color-stars;
}

.rating__active {
    position: absolute;
    width: 0%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden;
}

.rating__active::before {
    content: '★★★★★';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;

    color: $color-stars;
}

.rating__items {
    display: flex;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

.rating__item {
    width: 20%;
    height: 100%;
    opacity: 0;
}